  margin-top: 40px;
}

nav {
  text-align: center;
  margin-top: 0;
}

nav ul {
  justify-content: center;
  padding: 0;
  list-style-type: none;
  display: flex;
}

nav ul li {
  margin-right: 20px;
}

nav ul li a {
  color: #fff;
  font-size: 1.2rem;
  text-decoration: none;
}

.search-container {
  justify-content: flex-end;
  align-items: center;
  gap: 10px;
  margin-top: 5%;
  margin-right: 0;
  display: flex;
}

