  padding: 10px 25px;
  display: flex;
}

#search-button {
  cursor: pointer;
  color: #000;
  opacity: .8;
  background-color: #fff;
  border: none;
  border-radius: 30px;
  padding: 10px 15px;
  transition: opacity .3s;
}

#search-button:hover {
  opacity: 1;
}

::placeholder {
  color: #000;
}

.logo {
  justify-self: start;
}

.logo img {
  width: 180px;
  height: auto;
}

.main-content {
  justify-content: space-between;
  padding: 20px;
  display: flex;
}

.content-wrapper {
  grid-template-columns: 1fr 2fr 1fr;
  gap: 20px;
  display: grid;
}

.main-article {
  text-align: center;
  background-color: #292929;
  flex-direction: column;
  gap: 20px;
  margin-top: 0;
  margin-bottom: 0;
  margin-left: 20px;
  display: flex;
}

.main-article img {
  max-width: 300px;
