  font-size: .9rem;
}

.other-articles {
  flex-direction: column;
  gap: 20px;
  padding: 20px;
  display: flex;
}

.other-articles .article {
  z-index: 1;
  background-color: #2b2b2b;
  border: 1px solid #fff;
  border-radius: 5px;
  align-items: flex-start;
  padding: 13px;
  transition: transform .3s;
  display: flex;
  position: relative;
  overflow: hidden;
}

.other-articles .article:before {
  content: "";
  z-index: -1;
  border-bottom: 5px solid #0000;
  border-right: 5px solid #0000;
  width: 0;
  height: 0;
  transition: width .3s, height .3s, border-color .3s;
  position: absolute;
  bottom: 0;
  right: 0;
}

.other-articles .article:hover:before {
  z-index: -1;
  border-bottom-color: #fff;
  border-right-color: #fff;
  width: 95%;
  height: 90%;
}

.other-articles .article:hover {
  color: #fff;
  z-index: 1;
  -webkit-backdrop-filter: blur(5px);
  background: #fff3;
  border: 1px solid #ffffff4d;
  transform: translate(-.25rem, -.25rem);
  box-shadow: 0 4px 30px #0000001a;
}

.image-container {
  flex-shrink: 0;
  display: flex;
}

.image-container img {
  object-fit: cover;
  border-radius: 5px;
  width: 200px;
  height: auto;
  max-height: 150px;
}

.image-container h2 {
  color: #fff;
  text-align: center;
  margin-bottom: 10px;
  margin-left: 10px;
  font-size: 1.3rem;
  font-weight: bold;
}

.article-content {
  flex-direction: column;
  gap: 10px;
  display: flex;
}

.article-info {
  text-align: left;
  flex-direction: column;
  justify-content: flex-start;
  display: flex;
}

.article-info p {
  text-align: left;
  color: #d0d0d0;
  font-size: 1rem;
  line-height: 1.3;
}

.article-content {
  align-items: flex-start;
  gap: 20px;
