  display: flex;
}

.author-abstract {
  text-align: left;
  flex-direction: column;
  display: flex;
}

.author-abstract p {
  color: #fff;
  font-size: 15px;
}

.abstract p {
  color: #fff0f0;
  font-size: .9rem;
}

.more-articles {
  flex-direction: column;
  gap: 10px;
  display: flex;
}

.more-articles a {
  color: #fff;
  text-decoration: none;
}

.suggestions {
  align-items: center;
}

.suggestions-list {
  z-index: 9999;
  background-color: #fffdfd98;
  border: #000;
  border-radius: 4px;
  width: 56%;
  max-height: 500px;
  margin-top: 30%;
  margin-left: 40%;
  position: absolute;
  left: 0;
