  overflow-y: hidden;
}

.suggestion-item {
  cursor: pointer;
  padding: 5px;
}

.suggestion-item:hover {
  background-color: #fff;
}

footer {
  text-align: center;
  background-color: #000;
  padding: 20px 0;
}

footer p {
  color: #fff;
  margin: 0;
  font-size: 1rem;
}

@media (width <= 768px) {
  .content-wrapper {
    flex-direction: column;
    grid-template-columns: 1fr;
    align-items: center;
    display: flex;
  }

  .main-content {
    flex-direction: column;
    align-items: center;
    margin-left: 30%;
    display: flex;
  }

  .main-article, .other-articles .article {
    width: 90%;
    margin: 10px 0;
  }

  header .title {
    width: 400px;
